//引入VueRouter
import VueRouter from 'vue-router'
//引入Luyou 组件
import ComNum from "../components/ComNum";
import QuickHit from "../components/QuickHit";
import Static from "../components/Static"
import Filter from "../components/Filter"
import Index from '../components/Index'
import Tip from '../components/Tip'
import System from '../components/System'
import Test from "../components/Test"

//创建router实例对象，去管理一组一组的路由规则
const router = new VueRouter({
  mode: "hash", 
  // mode:'history',  // 去掉url的 # 号
  routes: [
    {
      name: "ComNum",
      path: "/com-num",
      component: ComNum,
    },
    {
      name: "QuickHit",
      path: "/quick-hit",
      component: QuickHit,
    },
    {
      name: "Static",
      path: "/static",
      component: Static,
    },
    {
      name: "Filter",
      path: "/filter",
      component: Filter,
    },
    {
      path: "/",
      component: Index,
    },
    {
      path: "/tip",
      component: Tip,
    },
    {
      path: "/system",
      component: System,
    },
    {
      path: "/test",
      component: Test,
    },
  ],
});

//暴露router
export default router
