<template>
  <div class="box-detail">
    <!-- <div class="tip" v-if="details.length === 0">空空如也！</div> -->

    <div ref="list" class="list">
      <!-- <el-alert
        v-show="detailData.length === 0"
        title="空空如也，什么都没有！"
        type="warning"
        :center="true"
      >
      </el-alert> -->

      <el-empty :image-size="250" description="空空的，什么都没有下注！" v-if="detailData.length === 0"></el-empty>
      <el-row v-for="(d, index) in detailData" :key="d.time">
        <el-col :span="2">{{
          index + 1 > 9 ? index + 1 : "0" + (index + 1)
        }}</el-col>
        <el-col :span="8">{{ d.time }}</el-col>
        <el-col class="hao" :span="6">{{ d.haoMa }}</el-col>

        <el-col class="hao" :span="5"
          >{{ d.money }} || {{ d.item.length * d.money }}</el-col
        >
        <el-col :span="3"
          ><el-button
            size="mini"
            type="danger"
            icon="el-icon-delete"
            circle
            @click="deleteHao(index)"
          ></el-button
        ></el-col>
      </el-row>
    </div>
    
  </div>
</template>

<script>
  export default {
    name: "Detail",
    data() {
      return {
        detailData: [],
      };
    },

    created() {
      let { ymd } = this.getTime(new Date());
      this.detailData = JSON.parse(localStorage.getItem(ymd)).details;
  },
  activated() {
      this.$nextTick(() => {
            this.$refs.list.scrollTo({
              top: this.$refs.list.scrollHeight,
              behavior: "smooth",
            });
          });
    },
    mounted() {
      this.$bus.$on("updateDetails", (details) => {
        this.detailData = details;
      });
    },
    watch: {
      detailData: {
        // 初始化时让handler调用一下
        immediate: true,
        handler() {
          //下注后，详情页滚动到底部
          this.$nextTick(() => {
            this.$refs.list.scrollTo({
              top: this.$refs.list.scrollHeight,
              behavior: "smooth",
            });
          });
        },
      },
    },

    methods: {
      // 退码
      deleteHao(index) {
        this.$confirm(
          `确定删除 ${this.detailData[index].haoMa} = ${this.detailData[index].money} 吗?`,
          "",
          {
            confirmButtonText: "确定",
            cancelButtonText: "取消",
            type: "warning",
          }
        )
          .then(() => {
            const data = this.detailData[index];
            data.money = -data.money;

            console.log(data);

            this.$bus.$emit("update", data, index);

            console.log(index, this.detailData);

            // this.$message({
            //   type: "success",
            //   message: "删除成功!",
            //   center: true,
            //   duration: 1000,
            //   offset: 70,
            // });
            this.$notify({
              message: `删除成功！`,
              type: "success",
              position: "top-right",
              offset: 50,
              duration: 3000,
            });

            //更新数据
            // let { ymd } = this.getTime(new Date());
            // this.detailData = JSON.parse(localStorage.getItem(ymd)).details;
          })
          .catch(() => {
             this.$notify({
              message: `已取消删除！`,
              type: "info",
              position: "top-right",
              offset: 50,
              duration: 3000,
            });
          });
      },
      // 获取时间,ymd:年月日,hms:时分秒
      /**
       * {
       *    ymd:'',
       *    hms:''
       */
      getTime(date) {
        let month = zeroFill(date.getMonth() + 1); //月
        let day = zeroFill(date.getDate()); //日
        let hour = zeroFill(date.getHours()); //时
        let minute = zeroFill(date.getMinutes()); //分
        let second = zeroFill(date.getSeconds()); //秒

        //年月日
        let hms = hour + ":" + minute + ":" + second;

        let ymd = date.getFullYear() + "-" + month + "-" + day;

        return { ymd: ymd, ymdhms: hms };

        /**
         * 补零
         */
        function zeroFill(time) {
          if (time >= 0 && time <= 9) {
            return "0" + time;
          } else {
            return time;
          }
        }
      },
    },
  };
</script>

<style scoped>
  .list {
    /* background-color: #1c1a1a; */
    height: 505px;
    overflow: auto;
    padding: 10px 20px;
  }

  .el-row {
    line-height: 30px;
    border-bottom: solid 1px rgb(216, 219, 217);
    padding: 5px 0px;
    text-align: center;
  }
  .hao {
    text-align: left;
  }
</style>
