<template>
  <div class="index">
    {{ zi[flag] }}
  </div>
</template>

<script>
  export default {
    name: "Index",
    data() {
      return {
        flag: 0,
        zi: ["恭喜发财", "心想事成", "财源广进", "身体健康"],
        interval: 0,
      };
    },
    mounted() {
      this.interval = setInterval(() => {
        this.flag = (this.flag + 1) % this.zi.length;
      }, 2500);
      this.$message({
        type: "warning",
        message: "仅支持二字定",
        center: true,
        duration: 2000,
        offset: 70,
      });
    },
    beforeDestroy() {
      clearInterval(this.interval);
    },
  };
</script>

<style lang="css" scoped>
  .index {
    text-align: center;
    margin: 100px auto;
    width: 60px;
    /* height: 250px; */
    padding: 50px 15px;
    font-size: 60px;
    color: rgb(0, 0, 0);
    background-color: rgb(255, 0, 0);
    font-family: '华文行楷';
  }
</style>
